.headerBlock {
  background: url("https://tesla.com/ns_videos/model-3-social.jpg") no-repeat
    center center fixed;
  background-size: cover;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.headerBlock__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 380px;
  position: relative;
  top: 120px;
}

.headerBlock__infoText {
  text-align: center;
}

.headerBlock__infoText h1 {
  color: #393c41;
  font-weight: 500;
  font-size: 38px;
}

.headerBlock__infoText h4 {
  font-weight: 400;
  font-size: 15px;
  color: #5c5e62;
}

.headerBlock__infoText h4 span {
  padding-bottom: 4px;
  padding-top: 15px;
  border-bottom: 1px solid black;
  color: #393c41;
  cursor: pointer;
}

.headerBlock__infoText h4 span:hover {
  border-bottom-width: 2px;
  color: black;
}

.headerBlock__info__actions {
  margin-top: 330px;
  display: flex;
  gap: 20px;
}

.headerBlock__info__actions button {
  cursor: pointer;
  border: none;
  width: 260px;
  padding-top: 12px;
  padding-bottom: 12px;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: 600;
  outline: none;
}

.headerBlock__buttonPrimary {
  background-color: #393c41;
  color: white;
  opacity: 0.9;
}

.headerBlock__buttonSecondary {
  background-color: white;
  color: black;
  opacity: 0.8;
}
